.container {
  width: 12.71vw;
  // margin-left: 9.28vw;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: space-between;
  font-family: "Noto Sans TC";
  position: fixed;
 top:0;
 left:9.28vw;

}
.navbar-logo {
  margin: 13px;
  width: 40px;
  height: 40px;
}
.navbar-link {
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  color: var(--color-dark-black);

  .navbar-link__logo-active,
  .navbar-link__logo {
    width: 24px;
    height: 24px;
  }

  .navbar-link__logo-active {
    display: none;
  }

  &.router-link-active {
    color: var(--logo-color);
    .navbar-link__logo {
      display: none;
    }
    .navbar-link__logo-active {
      display: block;
    }
  }
  &:hover {
    background-color: var(--color-light-sliver);
    border-radius: 5px;
  }

  &__title {
    margin-left: 16.9px;
    font-size: 18px;
    line-height: 26px;
    font-weight: 700;
  }
}

.tweet-btn {
  cursor: pointer;
}

.logout-btn {
  margin-top: auto;
  margin-bottom: 16px;
}
