.modal-bg {
	padding: 0;
	background-color: rgba($color: #000000, $alpha: 0.5);
	position: fixed;
	top: 0;
	left: 0;
	z-index: 100;
	width: 100%;
	height: 100%;
}
.container {
	display: flex;
	flex-direction: column;
	border: 1px solid #e6ecf0;
	// border-bottom: 10px solid #e6ecf0
	font-family: 'Noto Sans TC';
	width: 44%;
	height: 64%;
	border-radius: 14px;
	background-color: #ffffff;
	position: relative;
	top: 4.67%;
	left: 24.7%;
	z-index: 200;
}
.close-box {
	padding: 19.5px;
	min-height: 54px;
	border-bottom: 1px solid #e6ecf0;
	margin: 0;
	svg path {
		fill: #ff6600;
	}
	.closer {
		border: none;
		background-color: transparent;
		color: inherit;
		padding: 0;
		margin: 0;
		text-decoration: none;
		font-size: inherit;
	}
}

.input-body {
	display: flex;
	margin-left: 15px;
	margin-top: 18px;
}
.user-avatar {
	// display: flex;
	// flex-direction: column;
	align-items: center;
	justify-content: space-between;
	.avatar-img {
		display: block;
		width: 50px;
		height: 50px;
		border-radius: 100px;
	}
	.user-avatar-bar {
		flex-grow: 1;
		background-color: #ccd6dd;
		width: 2px;
		height: 86px;
		margin: 5px auto;
	}
}
.tweet-content {
	margin-top: 10px;
	color: var(--color-dark-black);
	font-size: 16px;
	line-height: 26px;
	font-weight: 400;
	word-wrap: break-word;
}

.input-textarea {
	width: 100%;
	height: 100%;
	margin-left: 8px;
	resize: none;
	border: transparent;
	outline: none;
	font-size: 16px;
	line-height: 26px;
	font-weight: 700;
	// color: var(--color-secondary);
	font-family: 'Noto Sans TC';
}

.footer {
	position: absolute;
	bottom: 0;
	right: 0;
	margin-right: 16px;
	height: 70px;
	display: flex;
	align-items: center;
	justify-content: flex-end;
	.error-message {
		margin-right: 20px;
		color: var(--color-error);
		font-size: 15px;
		line-height: 15px;
		font-weight: 500;
	}
}
.tweet-btn {
	display: flex;
}
.btn-submit {
	border: none;
	background-color: transparent;
	color: inherit;
	padding: 0;
	margin: 0;
	text-decoration: none;
	font-size: inherit;
}
.loading {
	width: 25px;
	height: 25px;
	border: 2px solid rgba(67, 234, 108, 1);
	border-top-color: rgba(67, 234, 108, 0.2);
	border-right-color: rgba(67, 234, 108, 0.2);
	border-bottom-color: rgba(67, 234, 108, 0.2);
	border-radius: 100%;
	margin-right: 15px;
	align-self: center;

	animation: circle infinite 0.75s linear;
}
.loading-btn {
	text-align: start;
	padding: 8px 16px;
	background-color: var(--color-skyblue);
	border: none;
	border-radius: 50px;
	color: var(--color-white);
	font-size: var(--main-text-size);
}

@keyframes circle {
	0% {
		transform: rotate(0);
	}

	100% {
		transform: rotate(360deg);
	}
}

.tweet-box {
	display: flex;
	flex-direction: column;
	margin-left: 10px;
	width: 37vw;

	.tweet-user-info {
		display: flex;
		margin: 0 5px auto 0;

		.tweet-user-name {
			font-family: 'Noto Sans TC';
			font-weight: 700;
			font-size: 15px;
			line-height: 21.72px;
			color: #1c1c1c;
		}

		.tweet-user-account {
			margin-left: 5px;
			font-family: 'Noto Sans TC';
			font-weight: 500;
			font-size: 15px;
			line-height: 21.72px;
			color: #657786;
		}
	}

	.tweet-content {
		flex-grow: 1;
		position: relative;
		word-wrap: break-word;

		font-family: 'Noto Sans TC';
		font-style: normal;
		font-weight: 400;
		font-size: 15px;
		line-height: 22px;
	}

	.tweet-reply-alert {
		margin-top: 18px;
		font-family: 'Noto Sans TC';
		font-style: normal;
		font-weight: 500;
		font-size: 13px;
		line-height: 26.06px;
		color: #9197a3;
	}
}
