.follow-btn {
  padding: 8px 16px;
  color: var(--logo-color);
  border: 1px solid var(--logo-color);
  border-radius: 50px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  font-family: "Noto Sans TC";
  background-color: var(--color-white);
  cursor: pointer;
  &:hover {
    background-color: rgb(250, 218, 163);
    box-shadow: 0 0 3px 3px rgba(201, 196, 196, 0.8);
  }

}
.following-btn {
  padding: 8px 16px;
  color: var(--color-white);
  border: none;
  border-radius: 50px;
  background-color: var(--logo-color);
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  font-family: "Noto Sans TC";
  cursor: pointer;
  &:hover {
    background-color: rgb(250, 218, 163);
    box-shadow: 0 0 3px 3px rgba(201, 196, 196, 0.8);
  }

}
